.app-header {
  padding: 0 25px;
  background-color: #3572db;
  color: white;
  padding: 20px;
  box-shadow: 0px 10px 13px -7px #989898, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .link {
      display: flex;
      justify-content: center;
      gap: 40px;
      font-size: 1.3rem;
      margin: 0 auto;

      a {
        all: unset;
        cursor: pointer;

        transition: 0.4s;
        &:hover {
          transition: 0.4s;
          color: rgba(129, 215, 255, 0.829);
        }
      }
      .active-nav {
        color: rgba(129, 215, 255, 0.829);
      }
    }

    article {
      Button {
        all: unset;
        cursor: pointer;
        transition: 0.4s;
        &:hover {
          transition: 0.4s;
          color: rgba(255, 101, 101, 0.856);
        }
      }

      .logout{
        margin-top: 0;
      }
    }
  }
}
