.currency-converter {
    .calculator {
        border: 2px solid #747474;
        border-radius: 5px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .input-container {
            border-bottom: 2px solid #747474;
            display: flex;
            align-items: baseline;

            input {
                background: none;
                border: none;
                box-shadow: none;
                outline: none;

                padding: 20px 10px;
                padding: 10px 10px;
                width: 100%;
                color: #333;
                text-align: right;
                font-size: 40px;
                border-radius: 0;
            }

            span {
                color: #6a6a6a;
                font-size: 0.8rem;
            }
        }
    }

    .calc-button {
        background: rgba(0, 0, 0, 0.315);
        border: 1px solid #111;
        padding: 20px;
        color: white;
        border-radius: 5px;
        font-size: 22px;
        cursor: pointer;
    }

    .calculator-buttons {
        padding: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-gap: 15px;

        aside {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            p {
                margin: 0;
                text-transform: uppercase;
                letter-spacing: 0.5px;
            }
        }

        .btn-and-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;

            img {
                margin-top: 3px;
            }
        }
    }

    .is-zero,
    .is-clear {
        grid-column: span 3;
    }

    .is-clear {
        background: #3572db;
    }

    .is-equals {
        background: #28d060;
    }
}
