
.err-msg {
    background-color: #f443369e;
    text-align: center;
    width: 80%;
    padding: 20px;
    color: white;
    animation: fadein 0.25s ease-in-out;
    position: absolute;
    top: 40px;
    z-index: 1;

    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.success-msg {
    background-color: #009c41ba;
    text-align: center;
    width: 80%;
    padding: 20px;
    color: white;
    animation: fadein 0.25s ease-in-out;
    position: absolute;
    top: 40px;
    z-index: 1;

    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}
