@import url("https://fonts.googleapis.com/css2?family=Stick+No+Bills:wght@700&display=swap");

.signup {
    .card{
        background-color: unset;
    }
    .card-body {
        width: 50%;
        margin: 0 auto;

        h2 {
            font-family: "Stick No Bills", sans-serif;
            text-align: center;
            font-weight: bold;
            text-transform: uppercase;
            letter-spacing: 3px;
            font-size: 5rem;
            margin: 50px;
        }
    }
    .btn-primary {
        color: #fff;
        background-color: #064095;
        border-color: #064095;
        margin-top: 20px;
    }

    section {
        margin: 20px auto 20px auto;

        img {
            margin: 0 5px;
            cursor: pointer;
        }
    }
}
