.table-page {
    table {
        width: 90vw;
        margin: 25px auto;
        font-family: Arial, Helvetica, sans-serif;
        border-collapse: collapse;
        background-color: white;
        text-align: center;
        tr {
            border: 1px solid black;
            &:nth-child(even) {
                background-color: #f2f2f2;
            }

            &:hover {
                background-color: #ddd;
            }
            td {
                font-size: 14px;
            }

            input {
                width: 100%;
                text-align: left;
            }
        }

        td,
        th {
            border: 1px solid #ddd;
            padding: 1rem;
        }
    }
}
