.gallery {
    .upload-img {
        border: 1px solid #8080805e;
        width: 50%;
        padding: 20px 0;
        text-align: center;
        margin: 20px auto 0 auto;
        cursor: pointer;
        .MuiSvgIcon-root {
            width: 4rem;
            height: 4rem;
            color: #2b5ca5;
        }
    }

    section {
        aside {
            width: 50px;
            height: 50px;
            margin: 12px 0;
        }
    }

    .add-img {
        display: flex;
        flex-wrap: wrap;
        gap: 15px;
        text-align: center;
        margin-top: 40px;
        justify-content: center;
        article {
            text-align: left;
            p {
                margin: 0.5rem 0 0.5rem 0;
            }
            img {
                height: 300px;
                width: 300px;
            }
        }
    }

}
